import { FC, useCallback, useEffect, useMemo, useState } from "react";
import Advertisement from "../../advertisement";
import FeedCard from "../../feedCard";
import Recommended from "../../recommended";
import ShowMoreButton from "../../showMoreButton";
import { TFilteredNewsByAlias } from "../../../../types/api";
import { useMediaQuery } from "react-responsive";

type TSectionTemplate = {
  title: string;
  items: TFilteredNewsByAlias;
  onLoadMore: (skip: number) => void;
};

export const SectionTemplate: FC<TSectionTemplate> = ({
  title,
  items,
  onLoadMore: onLoadMoreProps,
}) => {
  const [section, setSection] = useState(0);

  const isNotDesktop = useMediaQuery({ minWidth: "320px", maxWidth: "1439px" });
  const firstSection = useMemo(() => {
    const slicedItems = items.slice(0, 4);
    return (
      <>
        {slicedItems.map((news, index) => (
          <FeedCard type={index === 0 ? "full" : "half"} {...news} />
        ))}
      </>
    );
  }, [items]);

  const secondSection = useMemo(() => {
    const slicedItems = items.slice(4, 19);
    return (
      <>
        {slicedItems.map((news) => (
          <FeedCard type={"half"} {...news} />
        ))}
      </>
    );
  }, [items]);

  const thirdSection = useMemo(() => {
    const slicedItems = items.slice(20, 36);
    return (
      <>
        {slicedItems.map((news) => (
          <FeedCard type={"half"} {...news} />
        ))}
      </>
    );
  }, [items]);

  const onLoadMore = useCallback(() => {
    onLoadMoreProps(section + 1);
    setSection((prevState) => prevState + 1);
  }, [onLoadMoreProps, section]);

  const additionalSections = useMemo(() => {
    const slicedItems = items.slice(37, 77);
    const renderItems = slicedItems.map((news) => (
      <FeedCard type={"half"} {...news} />
    ));

    return (
      <section className={"sectionPage__subContent subContent"}>
        <Advertisement />
        <Advertisement />
        <Advertisement />
        <Advertisement />
        <Advertisement />
        <Advertisement />
        {renderItems}
      </section>
    );
  }, [items]);

  return (
    <section className={"sectionPage"}>
      <h1 className={"sectionPage__heading"}>{title}</h1>
      <section className={"sectionPage__mainContent mainContent"}>
        {isNotDesktop ? (
          <section className={"mainContent__mainCards"}>
            <Advertisement />
            <Advertisement />
            <Advertisement />
            {firstSection}
          </section>
        ) : (
          <>
            <section className={"mainContent__asideCardsList asideCardsList"}>
              {firstSection}
            </section>
            <aside className={"mainContent__asideAds"}>
              <Advertisement />
              <Advertisement />
              <Advertisement />
            </aside>
          </>
        )}
      </section>
      <section className={"sectionPage__additionalContent additionalContent"}>
        <Advertisement />
        <Advertisement />
        <Advertisement />
        <Advertisement />
        <Advertisement />
        <Advertisement />
        <Advertisement />
        {secondSection}
      </section>
      {/*<section className={"sectionPage__recommendedContent recommendedContent"}>*/}
      {/*  <Recommended heading={""} />*/}
      {/*</section>*/}
      <section className={"sectionPage__subContent subContent"}>
        <Advertisement />
        <Advertisement />
        <Advertisement />
        <Advertisement />
        <Advertisement />
        <Advertisement />
        <ShowMoreButton onClick={() => onLoadMore()} />
        {thirdSection}
      </section>
      {section ? additionalSections : null}
    </section>
  );
};
