import { FC, useContext, useEffect, useState } from "react";
import "./SectionPage.scss";
import { useParams } from "react-router-dom";
import { getNewsByAlias } from "../../../../api/api";
import { NewsCategoriesContext } from "../../../../context/NewsCategoriesContext";
import { TFilteredNewsByAlias } from "../../../../types/api";
import { SectionTemplate } from "./Template";
import { filterNewsByAlias } from "../../../../utils";
import { SiteError, SiteLoader } from "../../SiteStates";

const TAKE_AMOUNT = 36;
const SectionPage: FC = () => {
  const { alias } = useParams();
  const [news, setNews] = useState<TFilteredNewsByAlias>([]);
  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);
  const categories = useContext(NewsCategoriesContext);
  const currentCategory = categories
    ? categories.find((category) => category.alias === alias)
    : null;

  /* eslint-disable */
  useEffect(() => {
    if (Number(currentCategory?.id)) {
      setLoading(true);
      getNewsByAlias(Number(currentCategory?.id)).then((res) => {
        if (res) {
          setError(false);
          setNews(filterNewsByAlias(res));
        } else {
          setError(true);
        }
      });
      setLoading(false);
    }
  }, [alias, currentCategory]);

  const onLoadMore = (skip: number) => {
    getNewsByAlias(Number(currentCategory?.id), skip, TAKE_AMOUNT).then((res) => {
      if (res) {
        setNews(prevState => [...prevState, ...filterNewsByAlias(res)]);
      }
    })
  }

  if (isError) {
    return <SiteError />
  }

  if (isLoading || !currentCategory || !news) {
    return <SiteLoader />
  }

  return <SectionTemplate title={currentCategory.title} items={news} onLoadMore={onLoadMore} />;
};

export default SectionPage;
