              {/*  className={*/}
              {/*    "socialMediaExpandedList__item socialMediaExpandedList__item_expanding"*/}
              {/*  }*/}
              {/*>*/}
              {/*  <Facebook />*/}
              {/*</div>*/}
              {/*<div*/}
              {/*  className={*/}
              {/*    "socialMediaExpandedList__item socialMediaExpandedList__item_expanding"*/}
              {/*  }*/}
              {/*>*/}
              {/*  <Twitter />*/}
              {/*</div>*/}
