import {
  FC,
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import "./SmallArticle.scss";
import Advertisement from "../../advertisement";
import Newsline from "../../newsline";
import Category from "../../category";
import FeedInfo from "../../feedInfo";
import Tag from "../../tag";
import Share from "../../share";
import { useMediaQuery } from "react-responsive";
import InfiniteScroll from "react-infinite-scroller";
import Loader from "../../loader";
import { getNewsById, getNewsLine } from "../../../../api/api";
import {
  calculateReadingTime,
  filterNewsById,
  filterNewsLine,
  plainHtmlToArray,
} from "../../../../utils";
import { TFilteredNewsById, TNewsLine } from "../../../../types/api";
import { useParams } from "react-router-dom";
import { NewsCategoriesContext } from "../../../../context/NewsCategoriesContext";
import DOMPurify from "dompurify";
import { SiteError, SiteLoader } from "../../SiteStates";
import { TemplateContent } from "./TemplateContent";

interface QueryParams {
  id: number;
}

const SmallArticle: FC = () => {
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [isError, setIsError] = useState<boolean>(false);

  const isMobile = useMediaQuery({ minWidth: 320, maxWidth: 767 });
  const { id } = useParams() as unknown as QueryParams;
  const [lastId, setLastId] = useState(id);
  const childArticleRefs = useRef<Element[]>([]);
  const [newsLine, setNewsLine] = useState<TNewsLine>([]);
  const [items, setItems] = useState<TFilteredNewsById[]>([]);
  const isItemsFetched = useMemo(() => items.length, [items]);
  const categories = useContext(NewsCategoriesContext);
  const sanitize = useCallback((string: string) => {
    return DOMPurify.sanitize(string, { ADD_TAGS: ["iframe"] });
  }, []);

  const observerCallback = (entries: any) => {
    entries.forEach((entry: any) => {
      if (entry.isIntersecting) {
        history.pushState(
          { articleType: "xyu" },
          "",
          `/news/${entry.target.getAttribute("data-id")}`
        );
      }
    });
  };

  useEffect(() => {
    childArticleRefs.current.map((ref) => {
      if (ref) {
        const observer = new IntersectionObserver(
          (entries) => {
            observerCallback(entries);
          },
          { threshold: 0.25 }
        );
        observer.observe(ref);
      }
    });
  }, [items]);

  const fetchMoreData = useCallback(async () => {
    try {
      const data = await getNewsById(lastId);

      if (data) {
        setItems([...items, filterNewsById(data)]);
        setLastId((prevId) => prevId - 1);
      }
    } catch (e) {
      setIsError(true);
    }
  }, [items, lastId]);

  const fetchInitialData = useCallback(async () => {
    try {
      setIsLoading(true);
      const data = await getNewsById(id);

      if (data) {
        console.log(data);
        setItems([...items, filterNewsById(data)]);
        setLastId((prevId) => prevId - 1);
      }
    } catch (e) {
      console.log(e);
      setIsError(true);
    } finally {
      setIsLoading(false);
    }
  }, [id, items]);
  /* eslint-disable */
  useEffect(() => {
    fetchInitialData();
  }, []);

  useEffect(() => {
    getNewsLine("new").then((res) => {
      if (res) {
        setNewsLine(filterNewsLine(res));
      }
    });
  }, []);

  if (isError && !isItemsFetched) {
    return <SiteError />
  }

  if (isLoading && !isItemsFetched) {
    return <SiteLoader />
  }

  return (
    <InfiniteScroll
      pageStart={0}
      loadMore={fetchMoreData}
      initialLoad={true}
      hasMore={!isError}
      style={{
        width: "100%",
        margin: "0 auto",
      }}
      loader={
        <div
          style={{
            display: "flex",
            width: "100%",
            height: "200px",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Loader/>
        </div>
      }
    >
      {items.map((newsItem, index) => {
        const { heading, imgSrc, id, id_menu, date_pub_ins, views, text: newsText } = newsItem
        const currentCategory = categories?.find((category) => category.id === Number(id_menu))?.title
        const sanitizedText = sanitize(newsItem.text);
        const textItems = plainHtmlToArray(sanitizedText);

        return (
          <section
            ref={(el: HTMLElement) => (childArticleRefs.current[index] = el)}
            data-id={id}
            key={id}
            className={"smallArticle"}
          >
            <section className={"smallArticle__content"}>
              <section className={"smallArticle__mainPart mainPart"}>
                <section className={"mainPart__mainContent mainContent"}>
                  <Category name={currentCategory || ''} />
                  <h2 className={"mainContent__heading"}>{heading}</h2>
                  <div className={"mainContent__stats"}>
                    <FeedInfo
                      views={views}
                      type={"all"}
                      comments={123}
                      readingTime={calculateReadingTime(newsText)}
                      date={date_pub_ins}
                    />
                  </div>
                  <div className={"mainContent__mainImage"}>
                    <img src={imgSrc} alt={" "}/>
                  </div>
                  <div className={"mainContent__tags tags"}>
                    {(() => {
                      const currentCategory = categories?.find((category) => category.id === Number(id_menu));
                      if (!currentCategory) {
                        return <Tag name={""}/>
                      }
                      return (
                        <Tag name={currentCategory.title} path={`/section/${currentCategory.alias}`}/>
                      )
                    })()}
                  </div>
                  <section className={"mainContent__article article"}>
                    <TemplateContent items={textItems} />
                    <Share />
                  </section>
                </section>
                <aside className={"mainPart__aside aside"}>
                  <Advertisement />
                  <Advertisement />
                  <Advertisement />
                  {!isMobile && index === 0 && <Newsline heading={"  "} feed={newsLine} />}
                  <Advertisement />
                  <Advertisement />
                  <Advertisement />
                </aside>
              </section>
              <section className={"smallArticle__ads ads"}>
                <Advertisement />
                <Advertisement />
                <Advertisement />
              </section>
              {/*<section*/}
              {/*  className={*/}
              {/*    "smallArticle__recommendedArticles recommendedArticles"*/}
              {/*  }*/}
              {/*>*/}
              {/*  /!*<Recommended heading={" "} />*!/*/}
              {/*</section>*/}
            </section>
          </section>
        )
      })}
      {isError && <SiteError />}
    </InfiniteScroll>
  );
};

export default SmallArticle;
